<template>
  <div
    v-loading="loading"
    element-loading-text="拼命加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <el-row>
      <el-col :span="4">
        <div class="img-responsive">
          <img :src="`img/avatar/${gundam.ID}.gif`" style="width:100px">
          头像url:
          <el-input v-model="gundam.avatarURL" placeholder="请输入内容"></el-input>
        </div>
      </el-col>
      <el-col :span="6">
        <!-- <h2 class="h2">{{gundam.model}}</h2> -->
        Name：
        <el-input v-model="gundam.Name" placeholder="请输入内容"></el-input>
        <div>Rank:
          <!-- <el-input v-model="gundam.rank" placeholder="请输入内容"></el-input> -->
          <el-select v-model="gundam.rank" placeholder="请选择">
            <el-option
              v-for="item in formData.rank"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
        <div>{{gundam.landType}} {{gundam.fightType}}</div>
      </el-col>
      <el-col :span="10"></el-col>
    </el-row>
    <el-row>
      <table width="100%">
        <tr>
          <td>攻击</td>
          <td>防御</td>
          <td>机动</td>
          <td>操控</td>
          <td>4D综合</td>
          <td>3D综合</td>
        </tr>
        <tr>
          <td>{{gundam.att}}</td>
          <td>{{gundam.def}}</td>
          <td>{{gundam.fly}}</td>
          <td>{{gundam.ctl}}</td>
          <td>{{gundam.D4sum}}</td>
          <td>{{gundam.D3sum}}</td>
        </tr>
      </table>
    </el-row>
    <el-row>
      <!-- weapon -->
      <el-col :span="12">
        <el-table :data="gundam.weapon" :show-header="false">
          <!-- <el-table-column prop="weaponNum" label="weaponNum" ></el-table-column> -->
          <el-table-column prop="weaponNum" label="weaponNum">
            <template slot-scope="scope">
              <p style="font-size:10px; line-height:10px">{{scope.row.weaponNum}}：</p>
              {{scope.row.weaponName}}
            </template>
          </el-table-column>
          <el-table-column prop="weaponNum" label="weaponNum">
            <template slot-scope="scope">
              <img :src="scope.row.weapon_img" width="100">
            </template>
          </el-table-column>
        </el-table>
      </el-col>
      <el-col :span="12">
        <el-table :data="gundam.skill" :show-header="false">
          <el-table-column prop="skill_name" label="skill_name">
            <template slot-scope="scope">
              <img :src="scope.row.skill_img" width="30">
              <strong>{{scope.row.skill_name}}</strong>
              <br>
              <span>{{scope.row.skill_detail}}</span>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <!-- 合成需要 -->
    <p v-if="gundam.makeNeed&&gundam.makeNeed.length">合成需要：</p>
    <el-row type="flex" justify="space-between">
      <div class="img-responsive-sub" v-for="(robots) in gundam.makeNeed" :key="robots.ID">
        <a :title="robots.name" href="#" @click="updateGundam(robots)">
          <img :src="robots.img" :class="robots.role&&robots.role == 'key'? 'as-key':''">
          <p>{{robots.Level}}</p>
        </a>
      </div>
    </el-row>
    <!-- 国服合成需要 -->
    <p v-if="gundam.makeNeed_CN&&gundam.makeNeed_CN.length">国服合成需要：</p>
    <el-row type="flex" justify="space-between">
      <div class="img-responsive-sub" v-for="(robots) in gundam.makeNeed_CN" :key="robots.ID">
        <a :title="robots.name" href="#" @click="updateGundam(robots)">
          <img :src="robots.img" :class="robots.role&&robots.role == 'ke'? 'as-key':''">
          <p>{{robots.Level}}</p>
        </a>
      </div>
    </el-row>
    <!-- 用于合成 -->
    <p v-if="gundam.toMake&&gundam.toMake.length">用于合成：</p>
    <el-row v-if="gundam.toMake&&gundam.toMake.length" type="flex" justify="space-between">
      <div class="img-responsive-sub" v-for="(robots) in gundam.toMake" :key="robots.ID">
        <a :title="robots.name" href="#" @click="updateGundam(robots)">
          <img :src="robots.img" :class="robots.role&&robots.role == 'as-ke'? 'as-key':''">
          <p>{{robots.Level}}</p>
        </a>
      </div>
    </el-row>

    <!-- 扭蛋 -->
    <el-row>
      <span v-if="gundam.Machine&&gundam.Machine.length">扭蛋机：</span>
      <el-tag v-for="(Num, index) in gundam.Machine" :key="index">{{Num}}</el-tag>
    </el-row>
    <!-- 更多信息 -->
    <el-row>
      <p>其他信息：</p>
      <p>-{{gundam.force}}</p>
      <p>-{{gundam.feature}}</p>
      <p>-{{gundam.from}}</p>
      <p>-{{gundam.pilot}}</p>
      <p>{{gundam.story}}</p>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      loading: false,
      formData: {
        rank: ["S", "SS", "SR", "A", "AS", "AR","B","BS","BR","C","CS","CR"]
      }
    };
  },
  computed: {
    getshow: {
      get: function() {
        return this.show || false;
      },
      set: function() {
        this.$emit("dialogData", false);
      }
    }
  },
  props: {
    show: Boolean,
    gundam: Object
  },
  updated: function() {},
  methods: {
    updateGundam: function(robot) {
      this.loading = true;
      axios.post("/getGundam/findOne", robot).then(res => {
        this.gundam = res.data;
        this.loading = false;
        var div = document.querySelector(".el-dialog__wrapper");
        // div.scrollTop = div.scrollHeight;
        div.scrollTop = 0;
      });
    }
  },
  mounted: function() {
    this.updateGundam({ id: "15086" });
  }
};
</script>

<style>
.img-responsive {
  max-width: 100%;
  height: 110px;
  padding-bottom: 20%;
  overflow: hidden;
}
.img-responsive img {
  width: 42%;
}
.img-responsive-sub {
  max-width: 100%;
  height: 50px;
  padding-bottom: 10%;
  overflow: hidden;
}
.img-responsive-sub img {
  width: 80%;
}
.as-key {
  background: palevioletred;
}
</style>
